import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Gauge, Smartphone, Wifi, Battery, MapPin, Navigation, Truck } from "lucide-react";
import { useState, useEffect } from "react";
import { supabase } from "@/integrations/supabase/client";
import { useAuth } from "@/contexts/AuthContext";

export const DriverVehicleStatus = () => {
  const { user } = useAuth();
  const [speed, setSpeed] = useState(0);
  const [deviceConnected, setDeviceConnected] = useState(true);
  const [gpsSignal, setGpsSignal] = useState(100);
  const [vehiclePlate, setVehiclePlate] = useState<string | null>(null);

  useEffect(() => {
    loadVehicleData();
    
    const interval = setInterval(() => {
      setSpeed(Math.floor(Math.random() * 90) + 30);
    }, 3000);

    return () => clearInterval(interval);
  }, [user]);

  const loadVehicleData = async () => {
    if (!user) return;

    const { data: session } = await supabase
      .from('driver_work_sessions')
      .select('vehicle_plate')
      .eq('driver_id', user.id)
      .eq('status', 'ativa')
      .single();

    if (session) {
      setVehiclePlate(session.vehicle_plate);
    }
  };

  const getSpeedColor = () => {
    if (speed > 80) return "text-red-500";
    if (speed > 60) return "text-yellow-500";
    return "text-green-500";
  };

  return (
    <Card>
      <CardHeader>
        <CardTitle className="flex items-center gap-2">
          <Gauge className="h-5 w-5" />
          Status do Ve√≠culo
        </CardTitle>
      </CardHeader>
      <CardContent className="space-y-4">
        {/* Placa do Ve√≠culo */}
        {vehiclePlate && (
          <div className="flex items-center gap-3 p-4 bg-primary/10 rounded-lg border border-primary/20">
            <Truck className="h-8 w-8 text-primary" />
            <div>
              <p className="text-sm text-muted-foreground">Ve√≠culo</p>
              <p className="text-2xl font-bold">{vehiclePlate}</p>
            </div>
          </div>
        )}

        {/* Velocidade */}
        <div className="flex items-center justify-between p-4 bg-muted/50 rounded-lg">
          <div className="flex items-center gap-3">
            <Gauge className={`h-8 w-8 ${getSpeedColor()}`} />
            <div>
              <p className="text-sm text-muted-foreground">Velocidade Atual</p>
              <p className={`text-2xl font-bold ${getSpeedColor()}`}>
                {speed} km/h
              </p>
            </div>
          </div>
          {speed > 80 && (
            <Badge variant="destructive">Acima do limite</Badge>
          )}
        </div>

        {/* Status dos Dispositivos */}
        <div className="grid grid-cols-2 gap-3">
          <div className="flex items-center gap-2 p-3 bg-muted/50 rounded-lg">
            <Smartphone className={deviceConnected ? "h-5 w-5 text-green-500" : "h-5 w-5 text-red-500"} />
            <div className="flex-1">
              <p className="text-xs text-muted-foreground">Dispositivo</p>
              <Badge variant={deviceConnected ? "default" : "destructive"} className="text-xs mt-1">
                {deviceConnected ? "Conectado" : "Desconectado"}
              </Badge>
            </div>
          </div>

          <div className="flex items-center gap-2 p-3 bg-muted/50 rounded-lg">
            <Wifi className="h-5 w-5 text-blue-500" />
            <div className="flex-1">
              <p className="text-xs text-muted-foreground">Internet</p>
              <Badge variant="default" className="text-xs mt-1">
                4G Est√°vel
              </Badge>
            </div>
          </div>

          <div className="flex items-center gap-2 p-3 bg-muted/50 rounded-lg">
            <MapPin className="h-5 w-5 text-green-500" />
            <div className="flex-1">
              <p className="text-xs text-muted-foreground">GPS</p>
              <Badge variant="default" className="text-xs mt-1">
                {gpsSignal}% Sinal
              </Badge>
            </div>
          </div>

          <div className="flex items-center gap-2 p-3 bg-muted/50 rounded-lg">
            <Battery className="h-5 w-5 text-green-500" />
            <div className="flex-1">
              <p className="text-xs text-muted-foreground">Bateria</p>
              <Badge variant="default" className="text-xs mt-1">
                85%
              </Badge>
            </div>
          </div>
        </div>

        {/* Localiza√ß√£o Atual */}
        <div className="p-3 bg-primary/10 rounded-lg border border-primary/20">
          <div className="flex items-center gap-2 mb-2">
            <Navigation className="h-4 w-4 text-primary" />
            <p className="text-sm font-medium">Localiza√ß√£o em Tempo Real</p>
          </div>
          <p className="text-xs text-muted-foreground">
            Rodovia BR-101, Km 324 - S√£o Paulo, SP
          </p>
        </div>
      </CardContent>
    </Card>
  );
};
